import React, { Component } from 'react';
import { Map, GoogleApiWrapper, Marker, InfoWindow } from 'google-maps-react';
let maxsize="500px"
let minsize="300px"
const mapStyles = {

  borderRadius:"5px",
  margin: window.screen.width<600?"10px 0px 10px 0px":"10px 5px 5px 20px"

};

export default function Mapp(props){
    return<iframe 
    src= {props.map} 
    allowfullscreen="" 
    loading="lazy"
    height={window.screen.width<600?minsize:maxsize}
    width={window.screen.width<600?minsize:maxsize}
    style={mapStyles}>
    </iframe>
}