import React from "react";
import { Navbar } from "react-bootstrap";
import { Container } from "react-bootstrap";
import { Nav } from "react-bootstrap";
import { NavDropdown } from "react-bootstrap";
import { NavLink as Link } from "react-router-dom";

export default function NavBar (){

return<Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
<Container>
<Navbar.Brand ><Link className="nav-item navtitle" to="/" >Bramed salud</Link></Navbar.Brand>
<Navbar.Toggle aria-controls="responsive-navbar-nav" />
<Navbar.Collapse id="responsive-navbar-nav">
  <Nav className="me-auto navbar-nav me-auto mb-2 mb-lg-0">
    <Nav.Link>
      <Link className="nav-item navbutton" to="/plans" >
      Planes
      </Link>
      </Nav.Link>
  <Nav.Link>
    <Link className="nav-item navbutton" to="/sucursales" >
      Sucursales
      </Link>
  </Nav.Link>
  <NavDropdown title="Ver más..." id="collasible-nav-dropdown">
      <NavDropdown.Item href="#action/3.1">Conocé Bramed</NavDropdown.Item>
      <NavDropdown.Item href="#action/3.2">Nuestros valores</NavDropdown.Item>
      <NavDropdown.Divider />
      <NavDropdown.Item href="#action/3.4">Contacto</NavDropdown.Item>
    </NavDropdown>
  </Nav>
</Navbar.Collapse>
</Container>
</Navbar>
}