import React from "react";
import Data from "./Data";
import { Row, Col } from "react-bootstrap";
import Footer from "../DetailsHome/Footer";
import { useState, useEffect } from "react";
import { useParams } from "react-router";
import { Wrapper, Status } from "@googlemaps/react-wrapper";
import Mapp from "./Mapp"
const render = (status) => {
  return <h1>{status}</h1>;
};
export default function SucursalesDetails(props){  

const {key}= useParams()    
const [data, setData]=useState({})
useEffect(() => {
    setData(Data.find(e=>e.id==key))
  });

    return<Row>
    <Col xs={12} >
        <h2 style= {styles.title} className="titleFont">Sucursal en {data.lugar}:</h2>
    </Col>
    <Col xs={window.screen.width<600?13:5}> 
        <Mapp map={data.mapa} className="responsiveMap"/>
    </Col>
    <Col xs={window.screen.width<600?13:5} style={styles.span}> 
        <span>Dirección: 
        <br/> 
        {data.direccion}</span> 
        <br/>
        <span>Telefonos: 
        <br/>
        {data.telefonos}
        </span>
    </Col>
    <Footer/>
</Row>
}
const styles = {
    title:{
        textAlign:"left",
        margin: window.screen.width<600?"10px 0px 10px 5px":"10px 0px 15px 20px"
    },
    span:{
        textAlign:"left",
        margin: window.screen.width<600?"10px 0px 10px 5px":"10px 0px 10px 80px"
    }
}